/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.filter-panel {
  padding: 15px;
  border: 1px solid #b4b5b6;
  background: #f7f8f9;
}
@media only screen and (min-width: 901px) {
  .filter-panel {
    padding: 30px;
  }
}
.filter-panel hr {
  margin-top: 15px;
  margin-bottom: 15px;
  color: #b4b5b6;
}



