/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.search-panel {
  padding: 15px;
  border: 1px solid #b4b5b6;
  border-bottom: 0;
  background-color: #f7f8f9;
}
@media only screen and (min-width: 901px) {
  .search-panel {
    padding: 30px;
  }
}
@media only screen and (max-width: 599px) {
  .search-panel {
    padding: 10px;
  }
}
.date-subscript {
  font-size: 16px;
  color: #5a5d61;
}



