/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.modal-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(67, 72, 78, 0.75);
}
.modal-body {
  max-width: 480px;
  max-height: 100%;
  padding: 0;
  border: 1px solid #43484e;
  border-top: 12px solid #20aa3f;
  border-radius: 4px;
  margin: 45px;
  overflow: auto;
  background: #ffffff;
  outline: none;
}
.modal-body .header {
  padding: 15px;
  padding-bottom: 0;
  -ms-flex-align: start;
      align-items: flex-start;
}
.modal-body .header .a-btn__link {
  border: 0;
}
.modal-body .header .cf-icon {
  padding-left: 5px;
}
.modal-body .body {
  padding: 0 15px;
}
.modal-body .body p {
  margin-bottom: 30px;
}
.modal-body .body p:last-of-type {
  margin-bottom: 15px;
}
.modal-body .footer {
  padding: 15px;
  background-color: #f7f8f9;
  -ms-flex-align: baseline;
      align-items: baseline;
}
.modal-body .footer button {
  margin-right: 15px;
}
.modal-body .footer button.a-btn__link {
  height: 18px;
}



