/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.cf-select {
  position: relative;
  border: 1px solid #b4b5b6;
}
.cf-select select {
  width: 100%;
  padding: 0.25em 0.375em;
  padding-right: 1.7875em;
  border: 0;
  border-radius: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #ffffff;
  color: #101820;
}
.cf-select select:hover,
.cf-select select.hover,
.cf-select select:active,
.cf-select select:focus {
  outline: 2px solid #0072ce;
  outline-offset: 0;
}
.cf-select select[disabled] {
  color: #75787b;
  background-color: #e7e8e9;
  cursor: not-allowed;
}
.cf-select select[disabled]:hover {
  outline: none;
}
.cf-select select[disabled] option,
.cf-select select[disabled] option:disabled,
.cf-select select option:disabled {
  color: #e7e8e9;
}
.cf-select select::-ms-expand {
  display: none;
}
.cf-select:after {
  height: 100%;
  width: 1.625em;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #f7f8f9;
  color: #75787b;
  content: "\25BE";
  line-height: 1.625em;
  text-align: center;
  vertical-align: middle;
  pointer-events: none;
}
.no-csspointerevents .cf-select:after {
  height: 0;
  width: 0;
  border: 0;
  content: '';
}
.lt-ie10 .cf-select select {
  padding-right: 0.375em;
}
.action-bar {
  border: 1px solid #b4b5b6;
  border-left: 0;
  background-color: #e2efd8;
}
.action-bar .cf-select {
  padding-bottom: 0;
  border-left: 0;
  border-bottom: 0;
}
.action-bar .cf-select select {
  padding-top: 6.5px;
  padding-left: 15px;
  background-color: transparent;
  font-family: 'AvenirNextLTW01-Medium';
  font-size: 90%;
  text-transform: uppercase;
}
.action-bar .cf-select:after {
  background-color: #e2efd8;
}
.action-bar .cf-select #choose-size {
  padding-left: 30px;
}
.action-bar h2 {
  padding: 15px 30px;
  margin: 0;
}
@media only screen and (max-width: 599px) {
  .action-bar h2 {
    padding: 15px;
  }
}
.action-bar h4 {
  padding-left: 15px;
  padding-top: 5px;
  border-top: 1px solid #b4b5b6;
  margin: 0;
  margin-bottom: 5px;
}
.action-bar h4 .a-btn {
  border-bottom: dotted 1px #0050b4;
  color: #0050b4;
  font-family: 'AvenirNextLTW01-Medium';
  font-size: 14px;
  text-transform: uppercase;
}
.action-bar h4 .a-btn:hover {
  color: #7eb7e8;
}



