/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.light-box {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(67, 72, 78, 0.75);
}
.loading-box {
  max-width: 480px;
  padding: 30px;
  border: 1px solid #43484e;
  border-radius: 4px;
  overflow: auto;
  background: #f7f8f9;
  outline: none;
}
.loading-box span.cf-icon {
  padding-right: 10px;
}
.lt-ie10 .light-box {
  width: 100%;
  height: 100%;
  display: grid;
}
.lt-ie10 .light-box .loading-box {
  margin: auto;
  margin-top: 45px;
}



