/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.lt-ie10 .card-container nav .meaning {
  position: fixed;
  right: 15px;
}
.card-container {
  max-width: 100%;
  padding: 15px;
  margin-bottom: 30px;
}
.card-container nav {
  width: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.card-container nav a,
.card-container nav button {
  color: #0072ce;
  font-size: 16px;
}
.card-container nav a:hover,
.card-container nav button:hover {
  color: #4497dc;
}
.card-container nav .a-btn__link {
  border: 0;
}
.card-container nav .a-btn__link span {
  margin-right: 15px;
}
.card-container h1 {
  margin: 15px 0;
  margin-top: 30px;
}
.card-container h2.company-information {
  margin-top: 45px;
}
.card-container .card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
@media only screen and (max-width: 599px) {
  .card-container .card {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
}
.card-container .card .card-left {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 260px;
  width: 33%;
  padding-right: 30px;
}
@media only screen and (min-width: 600px) and (max-width: 900px) {
  .card-container .card .card-left {
    padding-right: 15px;
  }
}
@media only screen and (max-width: 599px) {
  .card-container .card .card-left {
    width: 100%;
    padding-right: 10px;
  }
}
.card-container .card .card-right {
  width: 67%;
}
@media only screen and (max-width: 599px) {
  .card-container .card .card-right {
    width: 100%;
    padding-top: 15px;
  }
}
.card-container .card .cf-icon__before {
  padding-right: 10px;
  font-size: 16px;
}
.card-container .card .cf-icon__before.cf-icon-approved-round {
  color: #20aa3f;
}
.card-container .card .cf-icon__before.cf-icon-delete-round {
  color: #d14124;
}
.card-container .card .cf-icon__before.cf-icon-minus-round {
  color: #ff9e1b;
}
.card-container .card .cf-icon__before.cf-icon-help-round {
  color: #254b87;
}
.card-container .card .cf-icon__before.cf-icon-error-round {
  color: #75787b;
}
.card-container .card .cf-icon__before.cf-icon-clock-round {
  color: #20aa3f;
}
.card-container .card .cf-icon__before.cf-icon-clock-round.not-timely {
  color: #ff9e1b;
}
.card-container .card .layout-column h3 {
  margin-top: 0;
  margin-bottom: 5px;
}
.card-container .card .layout-column h4 {
  margin-top: 0;
  margin-bottom: 5px;
}
.card-container .card .subitem {
  margin-right: 5px;
  font-family: 'AvenirNextLTW01-Medium';
}



