/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.content_hero {
  padding: 15px;
  margin-top: 30px;
}
@media only screen and (min-width: 901px) {
  .content_hero {
    padding: 30px;
    padding-top: 15px;
  }
}
@media only screen and (max-width: 599px) {
  .content_hero {
    padding: 10px;
  }
}
.content_hero p {
  max-width: 670px;
}
.content_hero ul {
  padding-top: 5px;
}
.content_hero li {
  padding-bottom: 0;
  border-bottom: 0;
}
.content_hero li button,
.content_hero li a {
  font-size: 16px;
}
.content_hero li.m-list_item {
  margin-right: 0;
}
.content_hero li::after {
  margin-left: 10px;
  margin-right: 10px;
  content: '|';
}
.content_hero li:last-of-type::after {
  margin: 0;
  content: '';
}
.content_hero .content-header {
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.content_hero h4.content-header {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-left: 10px;
  margin-top: 0;
  color: #43484e;
  text-transform: uppercase;
  vertical-align: text-bottom;
}
.lt-ie10 .content_hero h1.content-header {
  float: left;
}
.lt-ie10 .content_hero h4.content-header::after {
  content: "";
  clear: both;
  display: table;
}



