/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.aggregation-branch li.parent {
  position: relative;
  -ms-flex-align: baseline;
      align-items: baseline;
  list-style-type: none;
}
.aggregation-branch li.parent .toggle.a-label:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 20px;
  height: 20px;
  text-indent: 0;
}
.aggregation-branch li.parent .toggle.indeterminate:before {
  border: solid 2px #43484e;
  background: url('indeterminate.png') no-repeat scroll center center transparent;
}
.a-checkbox:checked + .aggregation-branch li.parent .toggle.a-label:before {
  border: solid 2px #43484e;
}
.aggregation-branch li.parent button {
  max-width: calc(100% - 95px);
  font-size: 16px;
  font-weight: normal;
  text-align: left;
  vertical-align: top;
}
.aggregation-branch li.parent button .cf-icon {
  padding-left: 5px;
}
.aggregation-branch li.parent .a-btn__link {
  border: 0;
}
.aggregation-branch li.parent .parent-count {
  position: absolute;
  top: 0;
  right: 0;
  color: #43484e;
  font-size: 14px;
}
.aggregation-branch ul.children {
  padding-left: 22.5px;
}
.lt-ie10 .aggregation-branch .flex-all {
  float: none;
}



