/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.date-filter h4 {
  margin-bottom: 0;
}
.date-filter ul.date-inputs {
  margin: 0;
}
.date-filter ul.date-inputs li {
  display: inline-block;
  width: calc(50% - 8px);
  list-style: none;
}
.date-filter ul.date-inputs li:first-of-type {
  margin-right: 15px;
}
.date-filter ul.messages {
  padding-top: 10px;
}
.date-filter ul.messages li {
  margin: 0;
  list-style: none;
}
.date-filter ul.messages .a-error-message .cf-icon {
  padding-right: 5px;
}



