/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.lt-ie10 .aggregation .layout-row {
  width: 100%;
}
.lt-ie10 .aggregation .flex-all {
  width: calc(100% - 95px);
}
.aggregation ul {
  padding-left: 0;
}
.aggregation ul li {
  padding: 0;
  border: 0;
  margin-bottom: 15px;
  position: relative;
  -ms-flex-align: baseline;
      align-items: baseline;
}
.aggregation ul li .a-label:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 20px;
  height: 20px;
  text-indent: 0;
}
.aggregation ul li .a-checkbox:checked + .a-label:before {
  border: solid 2px #43484e;
}
.aggregation ul li .bucket-key {
  max-width: calc(100% - 95px);
  padding-left: 30px;
  text-indent: -30px;
  vertical-align: top;
}
.aggregation ul li .bucket-count {
  position: absolute;
  top: 0;
  right: 0;
  color: #43484e;
  font-size: 14px;
}



