/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.export-modal .body .instructions {
  margin-bottom: 15px;
}
.export-modal .body .group {
  margin-bottom: 15px;
}
.export-modal .body .group .a-error-message .cf-icon {
  margin-right: 5px;
}
.export-modal .body .group .group__error {
  padding: 10px;
  padding-bottom: 0;
  border: solid 1px #d14124;
  margin-bottom: 15px;
}
.export-modal .body .group .group-title {
  margin: 15px 0;
  font-size: 18px;
  font-weight: bold;
}
.export-modal .body .group .m-form-field {
  margin-bottom: 15px;
}
.export-modal .body .group .m-form-field .multiline-label {
  display: inline-block;
}
.export-modal .body .group .other-formats {
  margin-top: 22.5px;
}
.export-modal .body .group .required-helper {
  color: #5a5d61;
}
.export-modal .body .timeliness-warning {
  margin-bottom: 22.5px;
  font-size: smaller;
}



