/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.typeahead {
  padding-bottom: 15px;
}
.typeahead input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
}
.typeahead input:focus {
  border: solid 2px #0072ce;
}
.typeahead .m-btn-inside-input .a-btn {
  position: absolute;
  right: 1em;
  top: calc(50% - 8px);
}
.typeahead .m-btn-inside-input .a-btn .cf-icon {
  padding-right: 4px;
}
.typeahead .typeahead-selector {
  position: relative;
  z-index: 99;
}
.typeahead .typeahead-selector ul {
  padding-left: 0;
  border: solid 2px #0072ce;
  border-top: 0;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.typeahead .typeahead-selector ul li.footer {
  padding: 15px;
  border-top: solid 1px #b4b5b6;
  margin: 0;
  background-color: #d6e8fa;
  cursor: default;
  font-size: 12px;
  font-style: italic;
  list-style-type: none;
  text-align: center;
}
.typeahead li.typeahead-option {
  padding: 15px;
  border-top: solid 1px #b4b5b6;
  margin: 0;
  background-color: #ffffff;
  cursor: default;
  list-style-type: none;
}
.typeahead li.typeahead-option:hover {
  border-top: solid 2px #0072ce;
  border-bottom: solid 2px #0072ce;
  background-color: #e7e8e9;
}
.typeahead li.typeahead-option.selected {
  border-top: solid 2px #0072ce;
  border-bottom: solid 2px #0072ce;
  background-color: #e7e8e9;
}
.input-contains-label_before ~ input[type="text"] {
  padding-left: 1.78571429em;
}
.input-contains-label_before {
  position: absolute;
  left: .85714286em;
  top: .5em;
  cursor: pointer;
  font-size: .875em;
}
.input-contains-label_before__search:before {
  font-family: 'CFPB Minicons';
  display: inline-block;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  content: "\e604";
}



