/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.lt-ie10 .search-bar .layout-row {
  width: 100%;
}
.lt-ie10 .search-bar .flex-all {
  width: calc(100% - 500px);
}
@media only screen and (max-width: 599px) {
  .search-bar h4,
  .search-bar .advanced-container {
    width: 0;
    display: none;
  }
}
.search-bar .cf-select {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 35px;
  background-color: #5a5d61;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
@media only screen and (max-width: 599px) {
  .search-bar .cf-select {
    width: 0;
    display: none;
  }
}
.search-bar .cf-select select {
  background-color: #5a5d61;
  color: #ffffff;
}
.search-bar .cf-select select:focus {
  outline: none;
}
.search-bar .cf-select:after {
  background-color: #5a5d61;
  color: #ffffff;
}
.search-bar button.a-btn.flex-fixed {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.search-bar .typeahead-portal {
  height: 30px;
  min-width: 12em;
  position: relative;
}
.search-bar .typeahead-portal .typeahead {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.search-bar .typeahead-portal .typeahead span.waiting,
.search-bar .typeahead-portal .typeahead span.error,
.search-bar .typeahead-portal .typeahead span.no-results {
  position: absolute;
  top: -25px;
  left: 0;
}
.search-bar .advanced-container {
  margin-left: 10px;
  width: 175px;
}
.search-bar h4 {
  margin-bottom: 5px;
}



