/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.pill {
  padding: 3px 5px;
  border-radius: 8px;
  margin-right: 15px;
  background-color: #d2d3d5;
  color: #101820;
  font-face: webfont-regular();
  font-size: 14px;
}
.pill button {
  padding: 0;
  padding-left: 5px;
  border: 0;
  border-left: 1px solid #101820;
  margin: 0;
  background-color: #d2d3d5;
  border-color: #d2d3d5;
  color: #101820;
}
.pill button:hover,
.pill button:active {
  background-color: #b4b5b6;
}



