/* ==========================================================================
   Capital Framework
   Color variables
   ========================================================================== */
.card-container .card .card-left {
  min-width: initial;
}
.lt-ie10 .card {
  display: table;
}
.lt-ie10 .card .card-left {
  float: left;
}
.lt-ie10 .card .card-right {
  width: calc(67% - 30px);
}
li.card-container {
  padding: 30px 0;
  border-bottom: 1px solid #b4b5b6;
  margin: 0;
  list-style-type: none;
}
li.card-container:first-of-type {
  padding-top: 0;
}
li.card-container:last-of-type {
  border: 0;
  padding-bottom: 0;
}
li.card-container .card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
@media only screen and (max-width: 599px) {
  li.card-container .card {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
}
li.card-container .card .card-left {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 260px;
  padding-right: 30px;
}
@media only screen and (min-width: 600px) and (max-width: 900px) {
  li.card-container .card .card-left {
    width: 50%;
    padding-right: 15px;
  }
}
@media only screen and (max-width: 599px) {
  li.card-container .card .card-left {
    width: 100%;
    padding-right: 10px;
  }
}
@media only screen and (max-width: 599px) {
  li.card-container .card .card-right {
    width: 100%;
    padding-top: 15px;
  }
}
li.card-container .card .layout-row .spacer {
  margin: 10px;
}
li.card-container .card .layout-row h4 {
  margin-right: 5px;
  font-size: 16px;
}
li.card-container .card .layout-column h3 {
  margin-top: 0;
  margin-bottom: 5px;
}
li.card-container .card .layout-column h3 a {
  border-bottom-width: 2px;
}
li.card-container .card .layout-column h3.to-detail {
  margin-bottom: 15px;
}
li.card-container .card .layout-column h4 {
  margin-top: 0;
  margin-bottom: 5px;
}
li.card-container .card .subitem {
  margin-right: 5px;
  font-family: 'AvenirNextLTW01-Medium';
}
li.card-container .card em {
  background-color: #d4e7e6;
  font-family: 'AvenirNextLTW01-Regular';
  font-style: normal;
}



